// 
import deviceInfoConfig from '@/views/company/DeviceManange/deviceInfoConfig';
// 
import deviceInfoBasics from '@/views/company/DeviceManange/deviceInfoBasics';
import deviceInfoExtend from '@/views/company/DeviceManange/deviceInfoExtend';
import deviceInfoCollect from '@/views/company/DeviceManange/deviceInfoCollect';
import { MSG, BTN_TEXT, CONSTANTS_DEVICE_CONFIGURATION } from '@/utils/constantsFile';
// 
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 
export default {
  components: {
    // 
    HeaderTitle,
    deviceInfoConfig,
    deviceInfoBasics,
    deviceInfoExtend,
    deviceInfoCollect,
    HeadOperationItem
  },
  props: {
  },
  data() {
    return {
      MSG: MSG,
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      BTN_TEXT: BTN_TEXT,
      // 
      operationLoad: false,
      // 
      tabPosition: 'deviceInfoBasics',
      // 
      currentView: 'deviceInfoBasics',
      // 
      showExtend: false
    };
  },
  computed: {},
  watch: {
    tabPosition(val) {
      this.currentView = val;
    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    // 
    buttonClickEvent(type) {
      if (type === 'exit') {
        this.goBack();
        return;
      }
      this[`${type}`]();
    },
    showInfo() {
      this.tabPosition = 'deviceInfoBasics';
      this.currentView = 'deviceInfoBasics';
      this.$refs.deviceInfoBasics.reset();
    },
    saveinfo() {
      if (this.tabPosition === 'deviceInfoBasics') {
        this.$refs.deviceInfoBasics.saveDeviceInfo();
      } else if (this.tabPosition === 'deviceInfoCollect') {
        this.$refs.deviceInfoCollect.saveDeviceInfo();
      } else if (this.tabPosition === 'deviceInfoConfig') {
        this.$refs.deviceInfoConfig.fullValidEvent();
      }
    },
    resetinfo() {
      if (this.tabPosition === 'deviceInfoBasics') {
        this.$refs.deviceInfoBasics.reset();
      } else if (this.tabPosition === 'deviceInfoCollect') {
        this.$refs.deviceInfoCollect.reset();
      } else if (this.tabPosition === 'deviceInfoConfig') {
        this.$refs.deviceInfoConfig.reset();
      }
    },
    /**
     * @description: 
     * @param val boolean
     * @return: 
     * @author: ZJM
     */
    toggleExtend(val) {
      this.showExtend = val;
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    goBack() {
      this.$emit('goBack', 0);
    },
    /**
     * @description: 
     * @param param 
     * @return: 
     * @author: ZJM
     */
    changeLoad(param) {
      this.operationLoad = param;
    }
  }
};
