import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// ztree
import ZTree from '@/components/common/ZTree/ZTree';
import {
  MSG,
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  CONSTANTS_DEVICE_CONFIGURATION
} from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
import {
  deviceGroupGetTreeFunc,
  deviceWorkshopTypeGetTreeFunc
} from '@/utils/publicFunction';
import {
  showWarningMessage,
  showErrorMessage,
  showSuccessMessage
} from '@/utils/message';
import {
  deviceGroupInsertOrUpdateBatchAction,
  deviceGroupDeleteByIdAction,
  deviceWorkshopTypeInsertOrUpdateBatchAction,
  deviceWorkshopTypeDeleteByIdAction
} from '@/api/management';
// 
import VueDraggable from 'vuedraggable';

/* import axios from 'axios';
import qs from 'qs'; */

export default {
  name: 'PageModifyUserGroup',
  components: {
    HeaderTitle,
    VueDraggable,
    ZTree
  },
  data() {
    return {
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      BTN_TEXT: BTN_TEXT,
      MSG: MSG,
      pageTitle: CONSTANTS_DEVICE_CONFIGURATION.CONFIG_PAGE,
      cardTitle: CONSTANTS_DEVICE_CONFIGURATION.CONFIG_CARD,
      panelTitle: CONSTANTS_DEVICE_CONFIGURATION.CONFIG_PANEL,
      // tab
      tabPosition: 'group',
      treeData: [],
      defaultProps: {
        children: 'children', // 
        label: 'name' // 
      },
      // 
      ztreeSelectedNode: {},
      // ztree
      ztreeObj: {},
      // ztree
      //   ztreeSelectedNode: {},
      // ztree
      nodeData: [],
      isInitTree: true,
      // 
      tempChildren: []
    };
  },
  watch: {
    // 
    tabPosition() {
      this.isInitTree = true;
      this.getTreeData();
    }
  },
  computed: {
    // id
    defaultExpandNodeKey() {
      const arr = [];
      if (Array.isArray(this.treeData) && this.treeData.length > 0) {
        this.treeData.map(({ id }) => {
          arr.push(id);
        });
      }
      return arr;
    }
  },

  methods: {
    /**
     * @description: ztree
     * @param obj ztree
     * @return: 
     * @author: ZJM
     */
    ztreeHandleCreated(obj) {
      // ztree
      this.ztreeObj = obj;
      if (this.ztreeSelectedNode.id >= -1) {
        var nodes = this.ztreeObj.getNodesByParam(
          'id',
          this.ztreeSelectedNode.id,
          null
        );
        this.ztreeSelectedNode = nodes[0];
        // 
        this.ztreeObj.selectNode(nodes[0]);
        // 
        this.getCurrentNode(nodes[0]);
        // 
        this.ztreeObj.expandNode(nodes[0], true);
      }
    },
    /**
     * @description: ztree
     * @param 
     * @return: 
     * @author: ZJM
     */
    ztreeOnClick(event, nodes, obj) {
      if (nodes) {
        this.ztreeSelectedNode = nodes;
      }
      if (this.tabPosition === 'group' && nodes.level === 8) {
        this.tempChildren = [];
      } else {
        // children
        if (nodes && nodes.children) {
          // this.tempChildren = [...data.children];
          this.tempChildren = JSON.parse(JSON.stringify(nodes.children));
        } else {
          this.tempChildren = [];
        }

        this.fillArrayToTargetNumber(this.tempChildren, 14);
      }
    },
    /**
     * @description: 
     * @param {array} arr 
     * @param {number} num 
     * @return: void
     * @author: cyb
     */
    fillArrayToTargetNumber(arr, num) {
      if (Array.isArray(arr)) {
        const length = arr.length;

        // target
        if (length < num) {
          for (let index = 0; index < num - length; index++) {
            arr.push({
              name: '',
              pid: this.ztreeSelectedNode.id
            });
          }
        }
      }
    },

    /**
     * @description: 
     * @param {object} node 
     * @return: void
     * @author: cyb
     */
    getCurrentNode(node) {
      this.ztreeSelectedNode = node;
      if (node.children) {
        this.tempChildren = JSON.parse(JSON.stringify(node.children));
        // this.tempChildren = [...node.children];
      } else {
        this.tempChildren = [];
      }
      this.fillArrayToTargetNumber(this.tempChildren, 14);
    },

    /**
     * @description: 
     * @param 
     * @return: void
     */
    async getTreeData() {
      var _that = this;
      if (this.tabPosition === 'group') {
        try {
          const res = await deviceGroupGetTreeFunc();
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            const groupData = [
              {
                id: -1,
                pId: 'x',
                type: 0,
                label: CONSTANTS_DEVICE_CONFIGURATION.ALL_GROUP,
                name: CONSTANTS_DEVICE_CONFIGURATION.ALL_GROUP,
                children: []
              }
            ];
            groupData[0].children = res.rows;
            this.nodeData = groupData;
            this.$nextTick(function() {
              // 
              if (this.isInitTree) {
                _that.isInitTree = false;
                _that.getCurrentNode(_that.nodeData[0]);
              }
            });
          } else {
            // 
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(TITLE.NOTIFY, error);
        } finally {
        }
      } else if (this.tabPosition === 'type') {
        try {
          const res = await deviceWorkshopTypeGetTreeFunc();
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            const groupData = [
              {
                id: -1,
                pId: 'x',
                label: CONSTANTS_DEVICE_CONFIGURATION.ALL_TYPE,
                name: CONSTANTS_DEVICE_CONFIGURATION.ALL_TYPE,
                children: []
              }
            ];
            groupData[0].children = res.rows;
            this.nodeData = groupData;
            this.$nextTick(function() {
              // 
              if (this.isInitTree) {
                this.isInitTree = false;
                this.getCurrentNode(this.nodeData[0]);
              }
            });
          } else {
            // 
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(TITLE.NOTIFY, error);
        } finally {
        }
      }
    },

    /**
     * @description: 
     * @param {number} index index
     * @return: void
     * @author: cyb
     */
    handleNodeAddEvent(index) {
      this.tempChildren.splice(index + 1, 0, {
        name: '',
        pid: this.ztreeSelectedNode.id
      });
    },
    /**
     * @description: 
     * @param {number} index index
     * @return: void
     * @author: cyb
     */
    async handleNodeDeleteEvent(index) {
      this.isInitTree = false;
      // index
      const lastValidNodeIndex = this.getLastValidNodeIndex();
      // indexlastValidNodeIndex
      if (index > lastValidNodeIndex) {
        return false;
      }
      const data = this.tempChildren[index];
      // id
      // indexlastValidNodeIndex
      if (!data.id) {
        this.tempChildren.splice(index, 1);
      } else {
        this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
          confirmButtonText: BTN_TEXT.CONFIRM,
          cancelButtonText: BTN_TEXT.CANCEL,
          type: 'warning'
        })
          .then(() => {
            this.setDelete(data.id);
            this.tempChildren.splice(index, 1);
            this.tempChildren.push({
              name: '',
              pid: this.ztreeSelectedNode.id
            });
          })
          .catch(() => {
            /*  */
          });
      }
    },
    async setDelete(id) {
      if (this.tabPosition === 'group') {
        try {
          const res = await deviceGroupDeleteByIdAction({
            id: id
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.getTreeData();
          } else {
            // 
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorMessage(error);
        }
      } else if (this.tabPosition === 'type') {
        try {
          const res = await deviceWorkshopTypeDeleteByIdAction({
            id: id
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.getTreeData();
          } else {
            // 
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorMessage(error);
        }
      }
    },
    /**
     * @description: 
     * @param {array} data 
     * @return: void | promise
     * @author: cyb
     */
    async postModifiedData(data) {
      if (this.tabPosition === 'group') {
        try {
          const res = await deviceGroupInsertOrUpdateBatchAction({}, data);
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.getTreeData();
          } else {
            // 
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorMessage(error);
        }
      } else if (this.tabPosition === 'type') {
        try {
          const res = await deviceWorkshopTypeInsertOrUpdateBatchAction(
            {},
            data
          );
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.getTreeData();
          } else {
            // 
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorMessage(error);
        }
      }
    },

    /**
     * @description: 
     * @param 
     * @return: number
     * @author: cyb
     */
    getLastValidNodeIndex() {
      let lastNotEmptyNodeIndex = this.tempChildren.length - 1;
      while (
        this.tempChildren[lastNotEmptyNodeIndex].name.trim() === '' &&
        lastNotEmptyNodeIndex >= 0
      ) {
        lastNotEmptyNodeIndex--;
      }

      return lastNotEmptyNodeIndex;
    },

    /**
     * @description: 
     * @param 
     * @return: void
     * @author: cyb
     */
    handleSaveEvent() {
      // 
      const index = this.getLastValidNodeIndex();

      // 
      if (index < 0) {
        showWarningMessage('');
        return false;
      }

      // 
      const hasEmptyNode = this.tempChildren.some(
        ({ name }, key) => name.trim() === '' && key < index
      );

      if (hasEmptyNode) {
        showWarningMessage(',');
        return false;
      }

      const data = [];

      // 
      for (let i = 0; i <= index; i++) {
        const item = {};
        const { name } = this.tempChildren[i];
        // 
        if (this.tabPosition === 'group') {
          item.parentId = this.ztreeSelectedNode.id;
        } else if (this.tabPosition === 'type') {
          item.typeParentId = this.ztreeSelectedNode.id;
        }
        item.sort = i + 1;
        if (this.tabPosition === 'group') {
          item.groupName = name;
          item.groupType = this.ztreeSelectedNode.type + 1;
        } else if (this.tabPosition === 'type') {
          item.typeName = name;
        }
        // item.userGroupName = name;
        if (this.tempChildren[i].id) {
          if (this.tabPosition === 'group') {
            item.groupId = this.tempChildren[i].id;
          } else if (this.tabPosition === 'type') {
            item.typeId = this.tempChildren[i].id;
          }
          // item.id = this.tempChildren[i].id;
        }
        data.push(item);
      }

      // 
      this.isInitTree = false;
      this.postModifiedData(data);
    },
    inputNull(index) {
      if (this.tempChildren[index].name === '') {
        this.tempChildren[index].name = this.ztreeSelectedNode.children[index].name;
        showWarningMessage('');
      }
    },
    /**
     * @description: 
     * @param null
     * @return: void
     * @author: cyb
     */
    returnMainPage() {
      this.$emit('goBack', 3);
    }
  },

  created() {
    this.getTreeData();
  }
};
