import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 
import { dataFormatter } from '@/utils/publicFunction';
// 
import moment from 'moment';
// 
import {
  deviceTypeGetTreeAction,
  deviceAdminDeviceUpdateById,
  deviceAdminSelectDeviceByCondition,
  deviceAdminDeviceParameterRelationshipDeviceRelationshipCollectType
} from '@/api/management';
import { STATUS_CODE, TITLE, MSG, VARIABLE, BTN_TEXT, CONSTANTS_DEVICE_CONFIGURATION } from '@/utils/constantsFile';
// 
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 
export default {
  components: {
    // 
    HeaderTitle,
    HeadOperationItem,
    moment
  },
  props: {
  },
  data() {
    return {
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      // 
      formInlineRules: {
        isCollect: [
          { required: true, message: MSG.PLEASE_CHOOSE_CONTENT, trigger: 'change' }
        ],
        isProductionCount: [
          { required: true, message: MSG.PLEASE_CHOOSE_CONTENT, trigger: 'change' }
        ]
      },
      // 
      formInline: {
        isCollect: 1,
        isProductionCount: 1,
        netSerialNumber: '',
        port: 0,
        ip: '',
        collectTypeId: null,
        ipAddress: [
          {
            value: ''
          }, {
            value: ''
          }, {
            value: ''
          }, {
            value: ''
          }
        ]
      },
      // id
      oldCollectId: 0,
      // 
      typeOptions: [],
      typeOptionsY: []
    };
  },
  computed: {},
  created() {
    this.getTypeTree();
  },
  mounted() {
  },
  methods: {
    /**
         * @description: cascader
         * @param obj ztree
         * @return: 
         * @author: ZJM
         */
    async getTypeTree() {
      try {
        const res = await deviceTypeGetTreeAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 
          this.typeOptions = dataFormatter(res.rows);
          this.typeOptionsY = res.rows;
          // 
          this.getDeviceInfo();
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    /**
   * @description: 
   * @param 
   * @return: 
   * @author: ZJM
   */
    async getDeviceInfo() {
      try {
        const res = await deviceAdminSelectDeviceByCondition({
          deviceCode: this.$store.state.deviceManangeCode
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.formInline.isCollect = res.rows[0].isCollect === undefined ? 0 : res.rows[0].isCollect;
          this.formInline.isProductionCount = res.rows[0].isProductionCount === undefined ? 0 : res.rows[0].isProductionCount;
          if (res.rows[0].deviceType > 0) {
            this.formInline.collectTypeId = this.$options.filters.getCascaderIds(res.rows[0].deviceType, this.typeOptionsY);
          } else {
            this.formInline.collectTypeId = null;
          }
          this.formInline.ip = res.rows[0].ip;
          this.formInline.port = res.rows[0].port;
          if (res.rows[0].ip) {
            if (res.rows[0].ip.split('.').length === 4) {
              this.formInline.ipAddress[0].value = res.rows[0].ip.split('.')[0];
              this.formInline.ipAddress[1].value = res.rows[0].ip.split('.')[1];
              this.formInline.ipAddress[2].value = res.rows[0].ip.split('.')[2];
              this.formInline.ipAddress[3].value = res.rows[0].ip.split('.')[3];
            } else {
              this.formInline.ipAddress[0].value = res.rows[0].ip.split('.')[0];
              this.formInline.ipAddress[1].value = '';
              this.formInline.ipAddress[2].value = '';
              this.formInline.ipAddress[3].value = '';
            }
          } else {
            this.formInline.ipAddress = [
              {
                value: ''
              }, {
                value: ''
              }, {
                value: ''
              }, {
                value: ''
              }
            ];
          }
          this.formInline.netSerialNumber = res.rows[0].netSerialNumber;
          if (res.rows[0].deviceType) {
            this.oldCollectId = res.rows[0].deviceType;
          }
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    saveDeviceInfo() {
      this.$refs.deviceInfo.validate(valid => {
        // 
        if (valid) {
          if (this.formInline.collectTypeId && this.formInline.collectTypeId.length > 0) {
            if (this.oldCollectId !== this.formInline.collectTypeId[this.formInline.collectTypeId.length - 1]) {
              this.$confirm(MSG.PROMPT_ABOUT_COLLECT, TITLE.PROMPT, {
                confirmButtonText: BTN_TEXT.CONFIRM,
                cancelButtonText: BTN_TEXT.CANCEL,
                type: 'warning'
              })
                .then(() => {
                  this.setSaveDeviceInfo(2); // 
                });
            } else {
              this.$confirm('?', TITLE.PROMPT, {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
              }).then(() => {
                this.setSaveDeviceInfo(2); // 
              }).catch(() => {
                this.setSaveDeviceInfo(1); // 
              });
            }
          } else {
            this.setSaveDeviceInfo(1);
          }
        }
      });
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    async setSaveDeviceInfo(paramx) {
      if (this.formInline.isCollect && !this.formInline.collectTypeId) {
        showWarningMessage('');
        return false;
      }

      try {
        this.formInline.deviceId = this.$store.state.deviceManangeId;
        this.formInline.deviceCode = this.$store.state.deviceManangeCode;
        var ipArr = this.formInline.ipAddress.map(val => {
          return val.value;
        });
        this.formInline.ip = ipArr.join('.');
        const res = await deviceAdminDeviceUpdateById(this.formInline);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 
          if (paramx === 2) {
            this.$emit('changeLoad', true);
            this.setDeviceType();
          } else {
            // 
            showSuccessMessage(res.message);
          }
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    async setDeviceType() {
      try {
        const res = await deviceAdminDeviceParameterRelationshipDeviceRelationshipCollectType({
          deviceCode: this.$store.state.deviceManangeCode,
          deviceTypeId: this.formInline.collectTypeId[this.formInline.collectTypeId.length - 1]
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.oldCollectId = this.formInline.collectTypeId[this.formInline.collectTypeId.length - 1];
          // 
          showSuccessMessage(res.message);
          this.$emit('changeLoad', false);
        } else {
          // 
          showWarningMessage(res.message);
          this.$emit('changeLoad', false);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
        this.$emit('changeLoad', false);
      } finally {
      }
    },
    reset() {
      this.$refs.deviceInfo.resetFields();
      this.getDeviceInfo();
    },
    // /**
    //  * @description: 
    //  * @param 
    //  * @return: 
    //  * @author: ZJM
    //  */
    // async clearParams() {
    //   try {
    //     const res = await deviceParameterRelationshipDeleteAction({}, [
    //       {
    //         column: 'deviceCode',
    //         operator: 1,
    //         ptype: 'string',
    //         type: 'eq',
    //         value: this.$store.state.deviceManangeCode
    //       },
    //       {
    //         column: 'isCustomizeParameter',
    //         operator: 1,
    //         ptype: 'int',
    //         type: 'eq',
    //         value: 0
    //       }
    //     ]);
    //     if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
    //       this.getCanSelectData();
    //       this.getDefinedData();
    //       this.getWarningData();
    //     } else {
    //       // 
    //       showWarningMessage(res.message);
    //     }
    //   } catch (error) {
    //     showErrorNotify(TITLE.NOTIFY, error);
    //   } finally {
    //   }
    // },
    // /**
    //  * @description: 
    //  * @param row 
    //  * @return: 
    //  * @author: ZJM
    //  */
    // async clearDeviceCollect() {
    //   try {
    //     const res = await deviceParameterRelationshipInsertOrUpdateAction({}, [
    //       {
    //         deviceCode: this.$store.state.deviceManangeCode,
    //         deviceId: this.$store.state.deviceManangeId,
    //         deviceType: ''
    //       }
    //     ]);
    //     if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
    //     } else {
    //       // 
    //       showWarningMessage(res.message);
    //     }
    //   } catch (error) {
    //     showErrorNotify(TITLE.NOTIFY, error);
    //   } finally {
    //   }
    // },
    // ip0-255
    checkIpVal(item) {
      // 0-255
      let val = item.value;
      // 
      val = val.toString().replace(/[^0-9]/g, '');
      val = parseInt(val, 10);
      if (isNaN(val)) {
        val = '';
      } else {
        val = val < 0 ? 0 : val;
        val = val > 255 ? 255 : val;
      }
      item.value = val;
    },
    // 
    turnIpPosition(item, index, event) {
      var self = this;
      var e = event || window.event;
      if (e.keyCode === 37) { // 
        if (index !== 0 && e.target.selectionStart === 0) {
          self.$refs.ipInput[index - 1].focus();
        }
      } else if (e.keyCode === 39 || e.code === 'NumpadDecimal') { // 
        if (index !== 3 && e.target.selectionStart === item.value.toString().length) {
          self.$refs.ipInput[index + 1].focus();
        }
      } else if (e.keyCode === 8) { // input
        if (index !== 0 && item.value === '') {
          self.$refs.ipInput[index - 1].focus();
        }
      } else if (e.keyCode === 13 || e.keyCode === 32 || e.keyCode === 190) { // 
        if (index !== 3) {
          self.$refs.ipInput[index + 1].focus();
        }
      } else if (item.value.toString().length === 3) { // 3
        if (index !== 3) {
          self.$refs.ipInput[index + 1].focus();
        }
      }
    },
    /**
     * @description: ip
     * @param {string} rule 
     * @param {string} value 
     * @param {function} callback 
     * @return: void
     * @author: ZJM
     */
    isIpValid(rule, value, callback) {
      var flag = 0;
      for (var item in value) {
        if (value[item].value === '') {
          flag++;
        }
      }
      if (flag > 0) {
        callback(new Error(CONSTANTS_DEVICE_CONFIGURATION.IP_VALID));
      } else {
        callback();
      }
    }
  }
};
