// 
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// ztree
import ZTree from '@/components/common/ZTree/ZTree';
// table
import VXETable from '@/components/common/Table';
// 
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 
import { STATUS_CODE, TITLE, MSG, BTN_TEXT, CONSTANTS_DEVICE_CONFIGURATION, VARIABLE, EXPORT_NAME } from '@/utils/constantsFile';
// 
// import moment from 'moment';
// 
import addDevice from '@/views/company/DeviceManange/addDevice.vue';
import deviceInfo from '@/views/company/DeviceManange/deviceInfo.vue';
import deviceGroupConfig from '@/views/company/DeviceManange/deviceGroupConfig.vue';
import SearchBox from '@/components/common/SearchBox';
import CustomColumn from '@/components/common/CustomColumn';
import ImportDialog from '@/components/common/ImportDialog';
import DialogSettingColumn from '@/components/common/DialogCustomizeColumns/DialogSettingColumn';
// 
// 
import {
  companyGetAllCompanyAndDevice,
  adminSwitchCompany,
  deviceAdminDeviceDeleteById
} from '@/api/management';
export default {
  name: 'DeviceConfiguration',
  components: {
    // 
    HeaderTitle,
    // 
    HeadOperationItem,
    // table
    VXETable,
    // 
    ZTree,
    // 
    addDevice,
    // 
    deviceInfo,
    deviceGroupConfig,
    SearchBox,
    CustomColumn,
    ImportDialog,
    DialogSettingColumn
  },
  data() {
    return {
      EXPORT_NAME: EXPORT_NAME,
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      // 
      deviceQRcode: '',
      deviceName: '',
      devSelectCode: false,
      SelectCompany: false,
      devSelectNode: {},
      // 
      formUpdate: {},
      // 
      HeadTitleUrl: '',
      // table
      HeadTitleName: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_LIST,
      // / ids
      searchIdArr: [],
      // table
      deviceChecks: [],
      tableName: 'device',
      // 
      radio: 1,
      // --
      radioType: 1,
      // 
      radioTypeDisabled: [1, 2, 3],
      // tab
      tabPosition: 'group',
      // 
      showModelItem: false,
      // 
      fatherNodeData: [],
      //  0 1
      showPage: 0,
      // 
      modelOptions: [],
      // 
      formParams: {
        model: null
      },
      // 
      formInline: {
        cnName: '',
        shortName: '',
        parameterName: '',
        isWarning: 'null'
      },
      // 
      rules: {
        name: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT, trigger: 'blur' }]
      },
      //  1  2 3
      operaType: 1,
      // label
      formLabelWidth: '150px',
      // label
      formLabelWidthShort: '50px',
      // 
      rightMenu: [],
      // 
      form: {
        name: ''
      },
      // 
      titlename: '',
      dialogVisibleQrCode: false,
      // 
      dialogVisibleUpdate: false,
      // 
      dialogVisible: false,
      // ztree
      ztreeObj: {},
      // ztree
      ztreeSelectedNode: {
        id: '-1',
        pId: '-1'
      },
      // ztree
      nodeData: [],
      // table
      tableLoading: false,
      // 
      searchData: [],
      // 
      srearchList: [],
      // 
      isSearchBoxShow: false,
      // 
      dialogVisibleCustomColumn: false,
      isImportDialogShow: false,
      ztreeName: '',
      companyId: 0,
      companySelectNode: {}
    };
  },
  computed: {
  },
  created() {
    this.getTree();
  },
  watch: {
  },
  methods: {

    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    deleteDev() {
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setDeleteChecks();
        })
        .catch(() => {
        });
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    async setDeleteChecks() {
      try {
        const res = await deviceAdminDeviceDeleteById({
          deviceId: this.$store.state.deviceManangeId,
          deviceCode: this.$store.state.deviceManangeCode,
          state: 0
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.ztreeObj.removeNode(this.devSelectNode);
          this.devSelectCode = false;
          showSuccessMessage(res.message);
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: ztree
     * @param event 
     * @param nodes 
     * @param obj ztree
     * @return: 
     * @author: ZJM
     */
    ztreeOnClick(event, nodes, obj) {
      // type 
      if (nodes.type === 0) {
        this.changeCompany(nodes.id);
      }
      console.log(nodes);
      if (nodes.pId === '0') {
        this.devSelectCode = false;
        // this.SelectCompany = true;
        // console.log(nodes.id);
        this.companyId = nodes.id;
        // console.log(this.companyId);
        this.companySelectNode = nodes;

        this.SelectCompany = false;
        setTimeout(() => {
          this.SelectCompany = true;
        }, 200);
      } else {
        this.devSelectCode = true;
        this.SelectCompany = false;
        this.devSelectNode = nodes;
        this.devSelectCode = nodes.code;
        this.$store.state.deviceManangeCode = nodes.code;
        this.$store.state.deviceManangeId = nodes.sbId;
        this.$refs.deviceInfo.showInfo();
      }
    },
    // 
    changeCompany(row) {
      console.log(row);
      this.toToggleComapny(row);
    },
    async toToggleComapny(param) {
      try {
        const res = await adminSwitchCompany(
          {
            companyId: param
          },
          {}
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 
     * @param row 
     * @return: 
     * @author: ZJM
     */
    getChildren(ids, treeNode) {
      ids.push(treeNode.id);
      if (treeNode.isParent) {
        for (var obj in treeNode.children) {
          this.getChildren(ids, treeNode.children[obj]);
        }
      }
      return ids;
    },
    /**
     * @description: ztree
     * @param obj ztree
     * @return: 
     * @author: ZJM
     */
    ztreeHandleCreated(obj) {
      // ztree
      this.ztreeObj = obj;
      if (this.ztreeSelectedNode.id && this.ztreeSelectedNode.id !== '-1') {
        var nodes = this.ztreeObj.getNodesByParam(
          'id',
          this.ztreeSelectedNode.id,
          null
        );
        this.ztreeSelectedNode = nodes[0];
        this.ztreeObj.selectNode(nodes[0]);
        this.ztreeObj.expandNode(nodes[0], true);
      }
      // this.findList();
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    async getTree() {
      try {
        const res = await companyGetAllCompanyAndDevice();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nodeData = res.rows;
          for (var i = 0; i < res.rows.length; i++) {
            if (res.rows[i].pId !== 0) {
              res.rows[i].sbId = res.rows[i].id;
              res.rows[i].id = '_' + res.rows[i].id;
            }
          }
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
      // }
    },
    addTreeDev(param) {
      console.log(param, this.companySelectNode);
      this.ztreeObj.addNodes(this.companySelectNode, param);
      this.SelectCompany = false;
      setTimeout(() => {
        this.SelectCompany = true;
      }, 200);
    }
  }
};
